@use "../setting" as setting;
@use "../global/mixin/" as mixin;
@use "../global/base/responsive" as res;

.body-contents-detail {
    background-color: setting.$white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .horizontal {
        display: flex;
        justify-content: center;

        .right {
            @include mixin.margin-16(left);
        }
        img {
            height: 250px;
        }
    }
    .vertical {
        display: flex;
        flex-direction: column;
    }
    header {
        position: fixed;
        height: auto;
    }
    .under-header {
        height: 126px;
        @include res.res("sp") {
            height: 60px;
        }
    }
    main {
        width: 80%;
        .top {
            width: 100%;
            margin: 0 auto;
            max-width: 700px;
            @include mixin.margin-32(top);
        }

        .middle {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            @include mixin.margin-16(top);
            .title {
                @include mixin.title();
                color: setting.$black;
            }
            .tags {
                display: inline-block;
                @include mixin.tags();

                @include mixin.margin-8(top);
                @include mixin.margin-8(bottom);
                @include mixin.padding-16(left);
                @include mixin.padding-16(right);
            }
            .discription {
                @include mixin.margin-16(top);
                @include mixin.discription();
                color: setting.$black;
            }
        }
        .bottom {
            @include mixin.margin-32(top);
        }
    }
    footer {
        @include mixin.margin-32(top);
        border-top: 1px solid setting.$black;
        height: 50px;
        width: 100%;
        display: flex;
        justify-content: end;
        a {
            font-family: setting.$gothic;
            font-size: 14px;
            font-weight: 700;
            color: setting.$black;
        }
    }
}
